$icon-color: #8255ca;
$icon-hover-color: #723dc7;
$arrow-size: 6px;
$min-width: 900px;

@import '../../../styles/vars.scss';
@import '@fortawesome/fontawesome-free/scss/fontawesome.scss';
@import '@fortawesome/fontawesome-free/scss/solid.scss';
@import '@fortawesome/fontawesome-free/scss/_animated.scss';

@import url('https://fonts.googleapis.com/css?family=Fira+Mono');

.training {
    display: grid;
    grid-template-columns: 400px 1fr;
    height: calc(100vh - 60px);
    overflow-y: hidden;
    min-width: $min-width;

    & > div {
        background: white;

        &.task-info {
            overflow-y: auto;

            &.ps--active-y {
                padding-right: 6px;
            }

            .title {
                position: sticky;
                z-index: 1;
                top: 0;
                background: white;
            }
        }

        &.task-editor {
            margin-left: 15px;
        }

        .questionbox,
        .resultbox,
        .tablesbox,
        .inputbox {
            position: relative;

            &:nth-last-child(n + 4) {
                padding-bottom: 10px;
            }
            .title,
            .content {
                padding: 10px 15px;
                // position: relative;
            }

            .title {
                font-weight: 600;
                border-bottom: 2px solid #efefef;
                font-size: 17px;
            }

            .content {
                line-height: 1.4;
            }

            .placeholder {
                padding: 7px 15px;
            }

            &[data-loading='true'] {
                min-height: 120px;
                &:after {
                    @extend %fa-icon;
                    @extend .fas;
                    animation: fa-spin 2s infinite linear;
                    font-size: 16px;
                    content: fa-content($fa-var-spinner);
                    margin-bottom: 1px;
                    position: absolute;
                    top: calc(50%);
                    left: calc(50% - 20px);
                    font-size: 40px;
                    color: #888;
                    z-index: 2;
                }
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: #fff;
                    opacity: 0.7;
                    z-index: 2;
                }
            }
        }

        .questionbox {
            position: relative;
            .title {
                padding: 10px 0;
                display: grid;
                grid-template-columns: 48px 1fr repeat(2, 25px);
                align-items: center;

                .menu-icon {
                    cursor: pointer;
                    height: 8px;
                    width: 20px;
                    margin-top: 2px;
                    justify-self: center;

                    &:hover {
                        &:before,
                        &:after {
                            background: $icon-hover-color;
                        }
                    }

                    &:before,
                    &:after {
                        content: '';
                        width: 20px;
                        height: 2px;
                        background: $icon-color;
                        position: absolute;
                    }

                    &:after {
                        margin-top: 4px;
                    }
                }

                .question__nav {
                    width: 15px;
                    height: 15px;
                    justify-self: left;
                    cursor: pointer;
                    color: $icon-color;

                    &:hover {
                        color: $icon-hover-color;
                    }
                }
            }

            .content {
                .show {
                    margin-top: 10px;
                }
            }
        }

        .tablesbox {
            .title {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .db-diagram-icon {
                    width: 15px;
                    color: $icon-color;
                    &:hover {
                        color: $icon-hover-color;
                    }
                }
            }

            .table {
                .table-title {
                    padding: 7px 15px;
                    cursor: pointer;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    position: relative;
                    font-weight: 600;
                    color: #555;

                    &:after {
                        @extend %fa-icon;
                        @extend .fas;
                        content: fa-content($fa-var-angle-down);
                        color: $icon-color;
                    }

                    &:hover {
                        &:after {
                            color: $icon-hover-color;
                        }
                    }

                    &.active {
                        background: #fafafa;
                        color: #333;

                        &:after {
                            transform: rotateZ(180deg);
                        }
                    }

                    &:hover {
                        background: #fafafa;
                    }
                }
                .table-props {
                    display: flex;
                    flex-direction: column;
                    overflow: hidden;
                    height: 0;

                    &.active {
                        height: auto;
                    }

                    .table-prop {
                        padding: 7px 15px;
                        padding-left: 35px;
                        position: relative;
                        word-break: break-all;
                        display: flex;
                        justify-content: space-between;
                        &.key {
                            &:before {
                                @extend %fa-icon;
                                @extend .fas;
                                content: fa-content($fa-var-key);
                                position: absolute;
                                left: 12px;
                                margin-top: 4px;
                                font-size: 14px;
                                color: #d6d639;
                            }
                        }
                    }
                }
            }
        }

        .inputbox {
            z-index: 2;
            background: white;

            &.pinned {
                position: sticky;
                top: 0;
            }

            .tabs {
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: 2px solid #efefef;
                & > div {
                    display: flex;
                    align-items: center;

                    .tab {
                        list-style: none;
                        font-weight: 600;
                        font-size: 17px;
                        padding: 10px;
                        margin-right: 5px;
                        border-radius: 3px 3px 0 0;
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                        &:hover {
                            background: #fafafa;
                        }
                        &.active {
                            background: #f6f6f6;
                            .tab-close {
                                display: block;
                            }
                        }

                        .tab-close {
                            height: 12px;
                            width: 12px;
                            vertical-align: unset;
                            margin-left: 10px;
                            display: none;
                        }
                    }
                }
                .tabs-icon {
                    width: 13px;
                    height: 13px;
                    padding: 10px;
                    border-radius: 3px;
                    cursor: pointer;
                    color: $icon-color;
                    transition: transform 0.15s ease-in-out;
                    &.pin {
                        margin-right: 10px;
                        transform: rotateZ(45deg);
                        opacity: 0.7;
                    }
                    &.pin-active {
                        transform: rotateZ(0deg);
                        opacity: 1;
                    }
                    &:hover {
                        opacity: 1;
                        color: $icon-hover-color;
                    }
                }
            }
            .textarea-scrollbar {
                height: 220px;
                background: #f6f6f6;
                border-radius: 0 0 0 3px;
                .ps__rail-y {
                    right: 8px !important;
                }
            }
            .textarea {
                padding: 10px 15px !important;
                min-height: 220px;
                font-family: 'Fira Mono', monospace;
                font-size: 15px;
                white-space: pre;
                textarea {
                    outline: none !important;
                    padding: inherit !important;
                    &::selection {
                        background: #d6d639 !important;
                    }
                }
            }
            .check-sql {
                border: none;
                background: #8255ca;
                color: white;
                border-radius: 25px;
                margin-top: -56px;
                right: 15px;
                position: absolute;
                outline: none;
                cursor: pointer;
                width: 40px;
                height: 40px;
                transition: all 0.2s ease-in-out;

                &:hover {
                    background: $icon-hover-color;
                }

                &:after {
                    @extend %fa-icon;
                    @extend .fas;
                    content: fa-content($fa-var-play);
                    margin-bottom: 1px;
                }

                &:disabled {
                    background: #777;
                    cursor: unset;
                }

                &[data-loading='true'] {
                    &:after {
                        @extend %fa-icon;
                        @extend .fas;
                        animation: fa-spin 2s infinite linear;
                        font-size: 16px;
                        content: fa-content($fa-var-spinner);
                        margin-bottom: 1px;
                    }
                }
            }
        }

        .resultbox {
            opacity: 1;
            transition: all 0.2s ease-in-out;
            &.checking {
                opacity: 0.2;
            }
        }
    }
}

.all-questions {
    position: fixed;
    left: -800px;
    top: 0;
    height: 100vh;
    width: 400px;
    background: #323252;
    color: #d4d5d6;
    z-index: 4;
    letter-spacing: 0.5px;
    transition: left 0.3s ease-in-out;
    overflow-y: auto;

    h2 {
        padding-left: 25px;
        color: white;
    }
    .question {
        padding: 15px 25px;
        &:hover {
            background: #3f3f5f;
            cursor: pointer;
            color: white;
        }
        &.active {
            background: #555581;
            cursor: pointer;
            color: white;
        }
    }
}

.all-questions-active {
    left: 0;
}

.all-questions-bg {
    position: fixed;
    z-index: 3;
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
    top: 0;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease-in-out;
}

.all-questions-bg-active {
    opacity: 1;
    pointer-events: unset;
}
